import React from 'react';
import PropTypes from 'prop-types';

const Job = ({ data }) => (
  <article className="jobs-container">
    <header>
    <div >
    <h4 style={{float: "right"}}>{data.location}</h4>
    <img src={data.icon} height={20} style={{float: "left", paddingRight: 5, verticalAlign: "middle", paddingTop: 5}}></img>
    <h3><a href={data.link}>{data.company}</a></h3>
    
    </div>
    <p className="daterange" style={{float: "right"}}>{data.daterange}</p>
      <h4>{data.position}</h4>
      
    </header>
    <ul className="points">
      {data.points.map((point) => (
        <li key={point}>{point}</li>
      ))}
      <br /><br />
    </ul>
  </article>
);

Job.propTypes = {
  data: PropTypes.shape({
    lab: PropTypes.string,
    link: PropTypes.string.isRequired,
    company: PropTypes.string.isRequired,
    position: PropTypes.string.isRequired,
    daterange: PropTypes.string.isRequired,
    points: PropTypes.arrayOf(PropTypes.string).isRequired,
  }).isRequired,
};

export default Job;
